import { VueCookieNext } from "vue-cookie-next";

const cookie = VueCookieNext;
const role = cookie.getCookie("role-data");

export const isMember = () => {
  return role === "member";
};

export const isAdmin = () => {
  return role === "admin";
};

export const isAffiliate = () => {
  return role === "affiliate";
};

export const isSales = () => {
  return role === "sales";
};

export const isOwner = () => {
  return role === "owner";
};

export const isTentor = () => {
  return role === "tentor";
};
