
import {
  defineAsyncComponent,
  defineComponent,
  onMounted,
  reactive,
  ref,
} from "vue";
import { dataRoles, dataStatus } from "./data-user";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import { helper } from "@/utils/helper";
import axios from "axios";
import { setBreadcrumb } from "@/utils/breadcrumb";
import { getRole } from "@/utils/cookie-next";
import { Breadcrumb } from "@/components/top-bar";

const ModalDetailUser = defineAsyncComponent(
  () => import("./components/modal-detail-user.vue")
);

const servicesV1Admin = async () => await import(`@/services/v1/admin/index`);

export default defineComponent({
  components: {
    ModalDetailUser,
  },
  setup() {
    // START --- setup breadcrumb -------------------------------------------------
    const dashboardPath = "/" + getRole();
    const breadcrumb: Breadcrumb[] = [
      {
        title: "Dashboard",
        to: dashboardPath,
      },
      {
        title: "Data User",
        to: `${dashboardPath}/data-user`,
        disabled: true,
      },
    ];

    onMounted(() => {
      setBreadcrumb(breadcrumb);
    });
    // END --- setup breadcrumb ---------------------------------------------------

    const config = api.configGet();
    const listRoles = dataRoles();
    const listStatus = dataStatus();

    const titleTable: Array<string> = [
      "Photo",
      "Nama",
      "Username",
      "Email",
      "Phone",
      "Status",
      "Role",
      "Created",
      "#",
    ];

    const dataUser = reactive({
      data: [] as any,
      page: 1,
      pageSize: 10,
      search: "",
      paginate: {
        currentPage: 1,
        endIndex: 4,
        endPage: 2,
        pageSize: 10,
        pages: [],
        startIndex: 0,
        startPage: 0,
        totalItems: 0,
        totalPages: 0,
      } as any,
    });

    const getData = async () => {
      const { getUser } = await servicesV1Admin();

      cash("#loadingDUadmin").show();

      try {
        const page = dataUser.page;
        const pageSize = dataUser.pageSize;

        const params = {
          page,
          pageSize,
          search: dataUser.search,
        };

        const hit = await getUser({ params } as any);

        dataUser.data = hit.response;

        const ds = hit.diagnostic;
        dataUser.paginate = gpf.paginate(ds.totalData, page, pageSize, 3);

        cash("#loadingDUadmin").hide();
      } catch (e: any) {
        cash("#loadingDUadmin").hide();
        await gpf.handleEds(e);
      }
    };

    onMounted(() => {
      getData();
    });

    const changePS = () => {
      getData();
    };

    const goToPage = (p: number) => {
      dataUser.page = p;
      getData();
    };

    const nextPage = () => {
      dataUser.page = dataUser.page + 1;
      getData();
    };

    const beforePage = () => {
      dataUser.page = dataUser.page - 1;
      getData();
    };

    const lastPage = () => {
      dataUser.page = dataUser.paginate.totalPages;
      getData();
    };

    const userId = ref(0 as number);
    const showDetailUser = (id: any) => {
      userId.value = id;
      gpf.showModal("#modal-detail-user");
    };

    return {
      titleTable,
      getData,
      dataUser,
      changePS,
      goToPage,
      nextPage,
      beforePage,
      lastPage,
      config,
      listRoles,
      listStatus,
      gpf,
      helper,

      userId,
      showDetailUser,
    };
  },
  data() {
    return {
      // for modal data user
      section: "add",

      exDataUser: {
        id: 0,
        name: "",
        email: "",
        phone: "",
        status: "1",
        roles: "member",
        username: "",
        pin: "",
        password: "",
      },

      required: {
        name: false,
        email: false,
        phone: false,
        status: false,
        roles: false,
        username: false,
        pin: false,
        password: false,
      },

      exDeleteUser: {
        id: 0,
        name: "",
      },
    };
  },
  methods: {
    changeTS(v: any, f: any) {
      const val = v.detail.args[1].key;
      const ex: any = this.exDataUser;
      ex[f] = val;
    },
    async emptyField() {
      const ex = this.exDataUser;
      ex.id = 0;
      ex.name = "";
      ex.email = "";
      ex.phone = "";
      ex.status = "1";
      ex.roles = "member";
      ex.username = "";
      ex.pin = "";
      ex.password = "";
    },
    requiredField(): boolean {
      const ex = this.exDataUser;
      const er = this.required;
      if (ex.pin) {
        er.pin = false;
        return true;
      } else {
        er.pin = true;
        return false;
      }
    },
    async addUser() {
      await this.emptyField();
      this.section = "add";
      gpf.showModal("#modal-data-user");
    },
    addUserApi() {
      gpf.gLoading.show();
      const ex = this.exDataUser;

      const body = {
        username: ex.username.trim(),
        password: ex.password ? ex.password : null,
        pin: ex.pin ? ex.pin : null,
        name: ex.name,
        email: ex.email,
        phone: ex.phone,
        status: ex.status,
        roles: ex.roles,
      };

      axios
        .post(api.adminDataUser, body, this.config)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          ex.name = "";
          ex.email = "";
          ex.phone = "";
          ex.roles = "";
          ex.status = "1";
          ex.username = "";

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-user");
          await this.emptyField();
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          // console.log("response add deu err ", error.response);
          await gpf.handleEds(error);
        });
      // if ( this.requiredField() ) {
      // }
    },
    async editUser(data: any) {
      await this.emptyField();
      this.section = "edit";

      gpf.gLoading.show();

      axios
        .get(api.adminDataUser + data.id, this.config)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          const rs = result.data.response;
          // console.log("response get deu ", rs);

          // console.log("rs.status.toLowerCase() ", rs.status.toLowerCase() , rs.status.toLowerCase() === "active")

          const ex = this.exDataUser;
          ex.id = rs.id;
          ex.name = rs.name;
          ex.email = rs.email;
          ex.phone = rs.phone;
          ex.roles = rs.role;
          rs.status.toLowerCase() === "active"
            ? (ex.status = "1")
            : (ex.status = "0");
          ex.username = rs.username;

          await gpf.showModal("#modal-data-user");
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          // console.log("response get deu err ", error.response);
          await gpf.handleEds(error);
        });
    },
    editUserApi(): void {
      gpf.gLoading.show();
      const ex = this.exDataUser;
      const body = {
        username: ex.username,
        password: ex.password ? ex.password : null,
        pin: ex.pin ? ex.pin : null,
        name: ex.name,
        email: ex.email,
        phone: ex.phone,
        status: ex.status,
        roles: [ex.roles],
      };

      axios
        .put(api.adminDataUser + ex.id, body, this.config)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          ex.name = "";
          ex.email = "";
          ex.phone = "";
          ex.roles = "";
          ex.status = "1";
          ex.username = "";

          await gpf.handleSds(result);
          await gpf.hideModal("#modal-data-user");
          await this.emptyField();
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          // console.log("response get deu err ", error.response);
          await gpf.handleEds(error);
        });
      // if ( this.requiredField() ) {
      // }
    },
    showPass(id: string) {
      const idHtml = cash("#" + id);
      if (idHtml.attr("type") === "password") {
        idHtml.attr("type", "text");
      } else {
        idHtml.attr("type", "password");
      }
    },
    deleteUserApi(data: any): void {
      const ex = this.exDeleteUser;
      ex.id = data.id;
      ex.name = data.name;

      gpf.showModal("#delete-modal-data-user");
    },
    fixDeleteUser() {
      const ex = this.exDeleteUser;

      gpf.gLoading.show();

      axios
        .delete(api.adminDataUser + ex.id, this.config)
        .then(async (result: any) => {
          gpf.gLoading.hide();

          ex.id = 0;
          ex.name = "";

          await gpf.handleSds(result);
          await gpf.hideModal("#delete-modal-data-user");
          await this.getData();
        })
        .catch(async (error: any) => {
          gpf.gLoading.hide();
          // console.log("response get deu err ", error.response);
          await gpf.handleEds(error);
        });
    },
  },
});
