import { AxiosRequestConfig, BaseResponse } from "@/services/types";
import { $api } from "@/services/constant";
import { HOST_API_V1 } from "@/services/env-init";
import { CatPembahasan, CatResult, DataCat, PayloadSubmitCat } from "./types";

const BASE_URL_MEMBER_CAT = HOST_API_V1 + "member/cat";

export const getMemberCatQuestion = async (
  type: string,
  config?: AxiosRequestConfig
): Promise<BaseResponse<DataCat>> => {
  const res = await $api().get(
    `${BASE_URL_MEMBER_CAT}/question/${type}`,
    config
  );
  return res.data;
};

export const postMemberCatSubmit = async (
  kodeExam: string,
  payload: PayloadSubmitCat
) => {
  const res = await $api().post(
    `${BASE_URL_MEMBER_CAT}/submit/${kodeExam}`,
    payload
  );
  return res.data;
};

export const getMemberCatResult = async (
  kodeExam: string
): Promise<BaseResponse<CatResult>> => {
  const res = await $api().get(`${BASE_URL_MEMBER_CAT}/result/${kodeExam}`);
  return res.data;
};

export const getMemberCatPembahasan = async (
  kodeExam: string
): Promise<BaseResponse<CatPembahasan>> => {
  const res = await $api().get(`${BASE_URL_MEMBER_CAT}/pembahasan/${kodeExam}`);
  return res.data;
};
