
import { defineComponent, onMounted, reactive, ref } from "vue";
import { useRoute, useRouter } from "vue-router";
// import { TypeHasilCat } from "./tryout-exam";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";
import {
  DataCat,
  MemberCatQuestion,
  PayloadSubmitCat,
} from "@/services/v1/member/cat/types";
import { isMember } from "@/utils/roles";
import {
  getMemberCatQuestion,
  postMemberCatSubmit,
} from "@/services/v1/member/cat";
import {
  getFeatureCatQuestion,
  postFeatureCatSubmit,
} from "@/services/v1/feature/cat";
import { tryoutPemantapan } from "@/views/admin/dashboard-admin/dashboard-admin";

type TypeHasilCat = {
  idSoal: number;
  nomor: number;
  ragu: string;
  opsi: string;
};

export default defineComponent({
  setup() {
    const route = useRoute();
    const router = useRouter();

    const params = route.params;
    const type = params.slug;
    const { jurusan, kode } = route.query;

    gpf.hideModal("#modal-cat-cpns");
    gpf.hideModal("#modal-cat-pppk");

    const toggleNavigasiSoal = (): void => {
      cash("#cvRn").toggleClass("transform rotate-180");
      cash("#nsRn").toggleClass("flex-wrap justify-center");
    };

    // START --- state data for cat ------------------------------------------------------
    const dataCat = reactive({
      kode: "",
      type: "",
      title: "",
      startDate: "",
      endDate: "",
      duration: 0,
      soal: [] as MemberCatQuestion[],
    } as DataCat);
    const currentSoal = ref({} as MemberCatQuestion);
    const allSoal = ref([] as MemberCatQuestion[]);
    const currentSoalIdx = ref(0 as number);
    const time = reactive({
      hours: "00",
      minutes: "00",
      seconds: "00",
    });
    const timeExpired = ref(false as boolean);
    const countDownDate = ref(0 as number);
    // END --- state data for cat --------------------------------------------------------

    // START --- for setup cat -----------------------------------------------------------
    const setDataCat = (cat?: DataCat) => {
      if (!cat) return;
      // console.log("cat ", cat);

      dataCat.kode = cat.kode;
      dataCat.type = cat.type;
      dataCat.title = cat.title;
      dataCat.startDate = cat.startDate;
      dataCat.endDate = cat.endDate;
      dataCat.duration = cat.duration;
      dataCat.soal = cat.soal;
    };

    const setupCat = async () => {
      try {
        let res: any = {};
        const params = jurusan && {
          jurusan,
        };

        if (isMember()) {
          res = await getMemberCatQuestion(type as string, { params });
        } else {
          res = await getFeatureCatQuestion(type as string, { params });
        }

        const resData = res?.response as DataCat;
        console.log("res ", resData);

        setDataCat(resData);

        allSoal.value = resData?.soal;
        currentSoal.value = resData?.soal[currentSoalIdx.value];
        countDownDate.value = new Date(resData.endDate).getTime();

        gpf.setUpPayloadSubmitHasilCat(resData.soal, resData.kode);
        gpf.setUpDataCat(resData, resData.kode);
        countTime();

        router.replace({
          path: route.fullPath,
          query: {
            jurusan,
            kode: resData.kode,
          },
        });
      } catch (err: any) {
        console.error("err getSoalCat ", err?.response);
      } finally {
        gpf.gLoading.hide();
      }
    };

    const countTime = () => {
      // console.log("masuk ke countTime ", this.countDownDate);
      // Update the count down every 1 second
      const x = setInterval(async () => {
        // Get today's date and time
        const now = new Date().getTime();

        // Find the distance between now and the count down date
        const distance = countDownDate.value - now;

        // Time calculations for days, hours, minutes and seconds
        // const days = Math.floor(distance / (1000 * 60 * 60 * 24));
        const hours = Math.floor(
          (distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
        );
        const minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
        const seconds = Math.floor((distance % (1000 * 60)) / 1000);

        time.hours = hours.toString();
        time.minutes = minutes.toString();
        time.seconds = seconds.toString();

        // If the count down is finished, write some text
        if (distance < 0) {
          clearInterval(x);
          timeExpired.value = true;

          await submitCat();
        }
      }, 1000);
    };

    onMounted(() => {
      setupCat();
    });
    // END --- for setup cat -------------------------------------------------------------

    // START --- for handling soal -------------------------------------------------------
    const changeSoal = (soal: any, idx: number): void => {
      currentSoal.value = soal;
      currentSoalIdx.value = idx;
    };

    const changeSoalCis = (tp: string): void => {
      tp === "next" ? (currentSoalIdx.value += 1) : (currentSoalIdx.value -= 1);
      currentSoal.value = allSoal.value[currentSoalIdx.value];
    };

    const chooseOpt = (cs: any, no: any, idx: any): void => {
      const a = cs.option;
      for (let i = 0; i < a.length; i++) {
        if (i !== idx) {
          cash('[opt="chs-' + no + "-" + i + '"]').removeClass("active");
        } else {
          cash('[opt="chs-' + no + "-" + idx + '"]').addClass("active");
          allSoal.value[currentSoalIdx.value].answer = cs.option[idx].option;
          allSoal.value[currentSoalIdx.value].answerIdx = idx;
          gpf.setUpPayloadSubmitHasilCat(allSoal.value, dataCat.kode);
        }
      }
    };

    const setRagu = () => {
      gpf.setUpPayloadSubmitHasilCat(allSoal.value, dataCat.kode);
    };
    // END --- for handling soal ---------------------------------------------------------

    // START --- for submit cat ----------------------------------------------------------
    const submitCat = async () => {
      const kodeExam = route.query?.kode;
      gpf.gLoading.show();

      try {
        let res: any = {};
        const jwbCat = JSON.parse(gpf.memberHasilCat(kodeExam as string));

        if (isMember()) {
          res = await postMemberCatSubmit(
            kodeExam as string,
            jwbCat as PayloadSubmitCat
          );
        } else {
          res = await postFeatureCatSubmit(
            kodeExam as string,
            jwbCat as PayloadSubmitCat
          );
        }

        console.log("res submitCat ", res);

        await gpf.removeStorageCat(kodeExam as string);

        router.push({
          path: `${type}/hasil`,
          query: {
            ...route.query,
          },
        });
      } catch (err: any) {
        console.error("err submitCat ", err?.response);
      } finally {
        gpf.gLoading.hide();
      }
    };
    // END --- for submit cat ------------------------------------------------------------

    return {
      params,
      toggleNavigasiSoal,
      route,
      router,
      type,

      gpf,
      dataCat,
      currentSoal,
      allSoal,
      currentSoalIdx,
      time,
      timeExpired,
      countDownDate,

      changeSoal,
      changeSoalCis,
      chooseOpt,
      setRagu,

      submitCat,
    };
  },
});
