
import { defineComponent, reactive } from "vue";
import { useRouter } from "vue-router";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";

export default defineComponent({
  components: {},
  setup() {
    const router = useRouter();
    const catCPNS = reactive({
      list: [],
      dataPG: {
        kode: "",
        title: "",
        nilaiPg: 0,
        jmlSoal: 0,
        durasi: 0,
        jurusan: [],
        selectJurusan: "",
      },
    });

    const saveUrl = async (data: any) => {
      gpf.gLoading.show();
      const hide = () => gpf.gLoading.hide();

      const url =
        (await api.getConfigFeatureMember(
          "memberPassingGrade",
          "featurePassingGrade"
        )) + data.kode;
      const hitApi = await api.hitApiGet(url, hide);
      const rs = hitApi.data.response;
      // console.log('response get pg ', rs);
      catCPNS.dataPG = rs;
      if (gpf.trimText(rs.jurusan)) {
        catCPNS.dataPG.jurusan = rs.jurusan;
        catCPNS.dataPG.selectJurusan = rs.jurusan[0].kode;
      } else {
        catCPNS.dataPG.jurusan = [];
        catCPNS.dataPG.selectJurusan = "";
      }
      gpf.showModal("#modal-cat-cpns");
    };

    const gotoTryoutExam = () => {
      gpf.hideModal("#modal-cat-cpns");
      gpf.gLoading.show();

      const kode = catCPNS.dataPG.kode;
      // console.log('catCPNS.dataPG ', catCPNS.dataPG);

      setTimeout(() => {
        const path = "cat-cpns/" + kode;
        if (catCPNS.dataPG.jurusan.length) {
          router.push({
            path,
            query: {
              jurusan: catCPNS.dataPG.selectJurusan,
            },
          });
        } else {
          router.push(path);
        }
      }, 700);
    };

    const getData = async () => {
      cash("#loadingCatCpns").show();
      const hide = () => cash("#loadingCatCpns").hide();

      const url =
        (await api.getConfigFeatureMember(
          "memberJenisTryout",
          "featureJenisTryout"
        )) + "cpns";
      const hitApi = await api.hitApiGet(url, hide);

      // console.log('hitApi ', hitApi);

      const rs = hitApi.data.response;

      catCPNS.list = rs;

      // console.log('catCPNS.list ', catCPNS.list);
    };

    getData();

    return {
      saveUrl,
      gotoTryoutExam,
      catCPNS,
      getData,
      gpf,
    };
  },
  methods: {
    changeTS(v: any, f: any) {
      const val = v.detail.args[1].key;
      const ex: any = this.catCPNS.dataPG;
      ex[f] = val;

      console.log("this.catCPNS ", this.catCPNS);
    },
  },
});
